import {AfterViewInit, Component, ElementRef, ViewChild} from '@angular/core';
import { NavInformation } from '../shared/nav-information.service';
import { AgmMap } from '@agm/core';
import { Http } from '@angular/http';
import { sendGet } from '../shared/http.resources';
import { Router } from '@angular/router';
import { UserInformation } from '../shared/user-information.service';
import { marker } from '../shared/types';
import {FormBuilder, FormGroup} from '@angular/forms';

// import {MapTypeId} from "@agm/core/services/google-maps-types";

let frontend: string = location.hostname === 'localhost' ? 'http://localhost:8080' : 'https://curbmap.com:443';
let backend: string = location.hostname === 'localhost' ? 'http://localhost:8081' : 'https://curbmap.com:50003';

@Component({
    selector: 'my-in',
    template: require('./in.component.html'),
    styles: [require('./in.component.scss')]
})
export class InComponent implements AfterViewInit {
    backend = location.hostname === 'localhost' ? 'http://localhost:8081' : 'https://curbmap.com:50003';
    mapTopLeft = {'lat': 34.040106800289944, 'lng': -118.23687258038635};
    center = {'lat': 34.040106800289944, 'lng': -118.23687258038635};
    zoom = 18;
    polylines = [];
    markers: marker[] = [];
    @ViewChild('map') map: AgmMap;
    @ViewChild('submitButton') submitButton: ElementRef;
    help: any = null;
    addInfo: any = null;
    getInfo: any = null;
    getInfoContent: any = null;
    marker1 = {lat: 0, lng: 0};
    marker2 = {lat: 0, lng: 0};
    timer: any;
    submitPreventDisabled = false;
    // image = null;
    addInfoForm: FormGroup;
    sizeLimit = 3000000;
    mapTypeIdx = 0;
    mapType = {value: 'roadmap', string: 'street'};
    mapTypes = [{value: 'roadmap', string: 'street' },
        { value: 'satellite', string: 'satellite' },
        { value: 'hybrid', string: 'both'}];
    restrSelector = [
        {'value': '', 'print': 'Select a restriction'},
        {'value': 'rednp', 'print': 'Red Zone-No Parking'},
        {'value': 'redns', 'print': 'Red Zone-No Stopping'}
    ];
    centerMarker: marker = null;
    all_day = false;
    time_start_am = 'am';
    time_end_am = 'am';
    hasTimeLimit = false;
    hasCost = false;
    markerToSet:marker = null;
    markerInfo:marker = null;
    addPoint = false;
    sunday = false;
    monday = false;
    tuesday = false;
    wednesday = false;
    thursday = false;
    friday = false;
    saturday = false;

    constructor(private navInformation: NavInformation,
                private http: Http,
                private router: Router,
                private fb: FormBuilder,
                private userInformation: UserInformation
    ) {
        this.createForm();
        this.addInfoForm.valueChanges.subscribe(data => this.onValueChanged(data));
        sendGet(this.http, {}, frontend + '/user', this.gotUser, this.gotNotLoggedIn).subscribe((result) => {
            if (result == []) {
                this.router.navigate(['/'], {skipLocationChange: true});
            } else {
                this.userInformation.setSESSION(result[1]);
                console.log(this.userInformation.getSESSION())
            }
            this.navInformation.setNavLinks('LoggedIn');
        });
        if (navigator.geolocation) {
            navigator.geolocation.getCurrentPosition(this.gotPosition.bind(this));
        }
        // // Get access to the camera!
        // if (navigator.mediaDevices && navigator.mediaDevices.getUserMedia) {
        //     // Not adding `{ audio: true }` since we only want video now
        //     navigator.mediaDevices.getUserMedia({ video: true }).then(function(stream) {
        //         console.log(stream);
        //     });
        // }
    }
    createForm() {
        this.addInfoForm = this.fb.group({
            time_startH: 0,
            time_startM: 0,
            time_startAm: false,
            time_endH: 0,
            time_endM: 0,
            time_endAm: false,
            restr: '',
            cost: '',
            per: '',
            timeLimit: '',
            all_day: false
        });
        this.resetForm();

    }
    onValueChanged(data: any){
        console.log(data);
        if (data['restr'] === 'time' || data['restr'] === 'timeCost') {
            this.hasTimeLimit = true;
        } else {
            this.hasTimeLimit = false;
        }
        if (data['restr'] === 'timeCost') {
            this.hasCost = true;
        } else {
            this.hasCost = false;
        }
        if (data['all_day']) {
            this.all_day = true;
        } else {
            this.all_day = false;
        }
    }
    clickDay(day) {
        switch(day) {
            case 0:
                this.sunday = !this.sunday;
                break;
            case 1:
                this.monday = !this.monday;
                break;
            case 2:
                this.tuesday = !this.tuesday;
                break;
            case 3:
                this.wednesday = !this.wednesday;
                break;
            case 4:
                this.thursday = !this.thursday;
                break;
            case 5:
                this.friday = !this.friday;
                break;
            case 6:
                this.saturday = !this.saturday;
                break;
        }
    }
    clickAllDays() {
        this.sunday = this.sunday ? this.sunday : !this.sunday;
        this.monday= this.monday ? this.monday : !this.monday;
        this.tuesday= this.tuesday ? this.tuesday : !this.tuesday;
        this.wednesday= this.wednesday ? this.wednesday : !this.wednesday;
        this.thursday= this.thursday ? this.thursday : !this.thursday;
        this.friday= this.friday ? this.friday : !this.friday;
        this.saturday= this.saturday ? this.saturday : !this.saturday;
    }
    clickMainMarker(event: any) {
        console.log("click main marker: " + event);
        this.markerToSet = this.centerMarker;
        this.resetForm();
        this.addInfo.modal('show');
    }
    changeMapType() {
        this.mapTypeIdx++;
        if (this.mapTypeIdx > this.mapTypes.length - 1) {
            this.mapTypeIdx = 0;
        }
        this.mapType = this.mapTypes[this.mapTypeIdx];
    }

    disableSendButton($event: any) {
        console.log('disable event: ' + $event);
        if (this.submitPreventDisabled !== true) {
            this.submitButton.nativeElement.disabled = true;
        }
        this.submitPreventDisabled = false;
    }

    imageRemoved($event: any) {
        console.log('image removed');
        console.log('event: ' + $event);
        this.submitPreventDisabled = true;
        this.submitButton.nativeElement.disabled = false;
    }

    imageUploaded($event: any) {
        console.log('image uploaded');
        console.log('event: ' + $event);
        this.submitPreventDisabled = true;
        this.submitButton.nativeElement.disabled = false;
    }

    resetForm() {
        this.hasTimeLimit = false;
        this.all_day = false;
        this.hasCost = false;
        this.sunday = false;
        this.monday = false;
        this.tuesday = false;
        this.wednesday = false;
        this.thursday = false;
        this.friday = false;
        this.saturday = false;
        let currentdate = new Date();
        let currentHours = this.convertToAMPM(currentdate.getHours()); // object { hr: 0-12, am: true/false }
        let currentMinutes = currentdate.getMinutes();
        if (currentHours.am) { this.time_start_am = 'am'; this.time_end_am = 'am'};
        this.addInfoForm.patchValue({
            restr: '',
            timeLimit: '',
            cost: '',
            per: '',
            time_startH: currentHours.hr,
            time_startM: currentMinutes,
            time_startAm: currentHours.am,
            time_endH: currentHours.hr,
            time_endM: currentMinutes,
            time_endAm: currentHours.am,
            all_day: false
        });
    }
    clickTime(object: string) {
        let temp: number;
        let am: boolean;
        switch (object) {
            case 'starthrup':
                temp = this.addInfoForm.value['time_startH'] + 1;
                am = this.addInfoForm.value['time_startAm'];
                if (temp > 11) {
                    am = !am
                }
                if (temp > 12) {
                    temp = 1
                }
                this.addInfoForm.patchValue({time_startH: temp, time_startAm: am});
                break;
            case 'startmnup':
                temp = this.addInfoForm.value['time_startM'] + 1;
                if (temp > 59) {
                    temp = 0;
                    this.addInfoForm.patchValue({time_startM: temp});
                    this.clickTime('starthrup');
                    break;
                } else {
                    this.addInfoForm.patchValue({time_startM: temp});
                    break;
                }
            case 'endhrup':
                temp = this.addInfoForm.value['time_endH'] + 1;
                am = this.addInfoForm.value['time_endAm'];
                if (temp > 11) {
                    am = !am
                }
                if (temp > 12) {
                    temp = 1
                }
                this.addInfoForm.patchValue({time_endH: temp, time_endAm: am});
                break;
            case 'endmnup':
                temp = this.addInfoForm.value['time_endM'] + 1;
                if (temp > 59) {
                    temp = 0;
                    this.addInfoForm.patchValue({time_endM: temp});
                    this.clickTime('endhrup');
                    break;
                } else {
                    this.addInfoForm.patchValue({time_endM: temp});
                    break;
                }
            case 'starthrdown':
                temp = this.addInfoForm.value['time_startH'] - 1;
                am = this.addInfoForm.value['time_startAm'];
                if (temp < 1) {
                    temp = 12;
                }
                if (temp == 11) {
                    am = !am
                }
                this.addInfoForm.patchValue({time_startH: temp, time_startAm: am});
                break;
            case 'startmndown':
                temp = this.addInfoForm.value['time_startM'] - 1;
                if (temp < 0) {
                    temp = 59;
                    this.addInfoForm.patchValue({time_startM: temp});
                    this.clickTime('starthrdown');
                    break;
                } else {
                    this.addInfoForm.patchValue({time_startM: temp});
                    break;
                }
            case 'endhrdown':
                temp = this.addInfoForm.value['time_endH'] - 1;
                am = this.addInfoForm.value['time_endAm'];
                if (temp < 1) {
                    temp = 12;
                }
                if (temp == 11) {
                    am = !am
                }
                this.addInfoForm.patchValue({time_endH: temp, time_endAm: am});
                break;
            case 'endmndown':
                temp = this.addInfoForm.value['time_endM'] - 1;
                if (temp < 0) {
                    temp = 59;
                    this.addInfoForm.patchValue({time_endM: temp});
                    this.clickTime('endhrdown');
                    break;
                } else {
                    this.addInfoForm.patchValue({time_endM: temp});
                    break;
                }
            default:
                break;
        }
        this.time_start_am = this.addInfoForm.value['time_startAm'] ? 'am' : 'pm';
        this.time_end_am = this.addInfoForm.value['time_endAm'] ? 'am' : 'pm';
    }
    clickAm(object: string) {
        if (object == 'start'){
            this.time_start_am = this.addInfoForm.value['time_startAm'] ? 'pm' : 'am';
            this.addInfoForm.patchValue({time_startAm: !this.addInfoForm.value['time_startAm']});
        } else {
            this.time_end_am = this.addInfoForm.value['time_endAm'] ? 'pm' : 'am';
            this.addInfoForm.patchValue({time_endAm: !this.addInfoForm.value['time_endAm']});
        }
    }
    convertToAMPM(hours: number) {
        let returnObj = {hr: 0, am: true};
        if (hours >= 12) {
            returnObj.am = false;
            if (hours > 12) {
                returnObj.hr = hours - 12;
            }
        } else {
            returnObj.hr = hours
        }
        return returnObj;
    }

    boundsChanged(data: any) {
        this.marker1.lng = data.b.b;
        this.marker1.lat = data.f.b;
        this.marker2.lng = data.b.f;
        this.marker2.lat = data.f.f;

    }

    ngAfterViewInit() {
        this.help = $('#modalHelp');
        this.addInfo = $('#modalAddInfo');
        this.getInfo = $('#modalGetInfo');
        this.getInfoContent = $('#modalGetInfoContent');
    }

    recenter() {
        navigator.geolocation.getCurrentPosition(this.gotPosition.bind(this));
    }

    showHelp() {
        this.help.modal('show');
    }

    gotPosition(data: any) {
        this.mapTopLeft.lat = data.coords.latitude;
        this.mapTopLeft.lng = data.coords.longitude;
        setTimeout(() => {
            this.map.triggerResize().then((fulfilled) => {
                console.log(fulfilled);
            });
        }, 0);
    }

    enteredIdle() {
        clearTimeout(this.timer);
        this.timer = setTimeout(this.getLines.bind(this), 10);
    }

    getLines() {
        console.log("XXX");
        let headers = {
            'Content-type': 'application/x-www-form-urlencoded; charset=utf-8',
            'Authorization': ' Bearer ' + this.userInformation.getToken(),
            'session': this.userInformation.getSESSION(),
            'Access-Control-Allow-Origin': '*'
        };
        // @11 = 1546m radius
        // @21 = 395m radius
        // let extent = this.map.zoom > 11 ? Math.exp(-1*this.map.zoom/17)*(25/(Math.pow(this.map.zoom,1.2)))*3800 : 0;
        if (this.map.zoom > 11 && this.map.zoom < 21) {
            const url = backend + '/areaPolygon' +
                '?lat1=' + this.marker1.lat +
                '&lng1=' + this.marker1.lng +
                '&lat2=' + this.marker2.lat +
                '&lng2=' + this.marker2.lng +
                '&user=' + this.userInformation.getUsername()
            ;
            sendGet(this.http,
                headers,
                url,
                this.gotData.bind(this),
                this.gotError.bind(this)).subscribe(
                result => {
                    this.polylines = [];
                    this.markers = [];
                    if (result !== []) {
                        for (let resultLine of result) {
                            let lineHolder = {line: [], color: 'black', restrs: [], weight: 0.5, opacity: 0.7};
                            resultLine['coordinates'].forEach((linePoint) => {
                                lineHolder.line.push({lat: linePoint[1], lng: linePoint[0]});
                            });
                            if (resultLine['restrs'] != null) {
                                let colOp = this.colorFromRestr([lineHolder.restrs]);
                                lineHolder.restrs = resultLine['restrs'];
                                lineHolder.color = colOp[0];
                                lineHolder.weight = 2;
                                lineHolder.opacity = colOp[1];
                            }
                            for (let pointIdx in resultLine['multiPointProperties']['points']) {
                                let colOp = this.colorFromRestr(resultLine['multiPointProperties']['restrs'][pointIdx])
                                let markerHolder: marker = {
                                    lng: resultLine['multiPointProperties']['points'][pointIdx][0],
                                    lat: resultLine['multiPointProperties']['points'][pointIdx][1],
                                    url: 'img/' + colOp[0] + '.png',
                                    opacity: parseFloat(colOp[1]),
                                    draggable: false,
                                    restriction: resultLine['multiPointProperties']['restrs'][pointIdx]
                                };
                                console.log(markerHolder);
                                this.markers.push(markerHolder);
                            }
                            this.polylines.push(lineHolder);
                        }
                    }
                },
                error => {
                    console.log("ERROR" + error);
                }
            );
        }
    }
    colorFromRestr(restrs: any[]): any[] {
        let color = 'black';
        let opacity = 0.5;
        for (let restr of restrs) {
            switch (restr[0]) {
                case 'hyd':
                case 'red':
                    color = 'red';
                    opacity = restr[2];
                    break;
                case 'sweep':
                    if (color !== 'red') {
                        color = 'purple';
                        opacity = restr[2];
                    }
                    break;
                case 'dis':
                    if (color !== 'red') {
                        color = 'blue';
                        opacity = restr[2];
                    }
                    break;
                default:
                    break;
            }
        }
        return [color, opacity];
    }
    makeTwoDigit(intValue) {
        if (intValue < 10) {
            return "0"+intValue
        } else {
            return ""+intValue
        }
    }
    onSubmit() {
        if (this.addInfoForm.value['restr'] === '')
            return;
        if ((this.addInfoForm.value['restr'] === 'time' || this.addInfoForm.value['restr'] === 'timeCost') && this.addInfoForm.value['timeLimit'] === '')
            return;
        if (this.addInfoForm.value['restr'] === 'timeCost' && (this.addInfoForm.value['cost'] === '' || this.addInfoForm.value['per'] === ''))
            return;
        let timeStart, timeEnd;
        if (this.all_day) {
            timeStart = "0000";
            timeEnd = "2400";
        } else {
            let timeStartH = this.makeTwoDigit(this.addInfoForm.value['time_startH']);
            let timeEndH = this.makeTwoDigit(this.addInfoForm.value['time_endH']);
            let timeStartM = this.makeTwoDigit(this.addInfoForm.value['time_startH']);
            let timeEndM = this.makeTwoDigit(this.addInfoForm.value['time_endM']);
            timeStart = timeStartH+timeStartM;
            timeEnd = timeEndH+timeEndM;
        }
        let restr = [];
        if ((parseInt(timeStart[0]) > parseInt(timeEnd[0])) ||
            (parseInt(timeStart[0]) === parseInt(timeEnd[0]) && parseInt(timeStart[1]) > parseInt(timeEnd[1])))
            return;
        restr.push(this.addInfoForm.value['restr']);
        restr.push(this.makeDays());
        restr.push(timeStart);
        restr.push(timeEnd);
        if (this.addInfoForm.value['restr'] === 'time' || this.addInfoForm.value['restr'] === 'timeCost') {
            restr.push(this.addInfoForm.value['timeLimit']);
            if (this.addInfoForm.value['restr'] === 'timeCost'){
                restr.push(this.addInfoForm.value['cost']);
                restr.push(this.addInfoForm.value['per']);
            }
        }
        let restrstring = restr.join(",");
        this.markerToSet['restriction'].push(restr);
        let headers = {
            "Authorization" : "Bearer " + this.userInformation.getToken()
        };
        let pointString = "lat="+this.markerToSet.lat+"&lng="+this.markerToSet.lng+"&rule="+btoa(restrstring);
        sendGet(this.http, headers, backend+"/addPoint?"+pointString, this.getResultAdd.bind(this), this.handleError.bind(this)).subscribe(addpoint => {
            console.log(addpoint);
            if (this.addPoint === true) {
                this.markerToSet['url'] = "img/" + this.colorFromRestr(restr)[0] + ".png";
                this.markers.push(this.markerToSet);
                this.markerToSet = null;
                this.addPoint = false;
            }
        });
        this.addInfo.modal('hide');
    }
    makeDays() {
        let days = [this.sunday?1:0,this.monday?1:0,this.tuesday?1:0,this.wednesday?1:0,this.thursday?1:0,this.friday?1:0,this.saturday?1:0];
        return days.join("");
    }
    getResultAdd(res: Response) {
        let jsonResponse = res.json();
        if (jsonResponse['success'] === true) {
            this.addPoint = true;
            return [true];
        } else {
            this.addPoint = false;
            return [];
        }
    }
    handleError(err: Response) {
        console.log("error: "+ err);
        this.addPoint = false;
        return [];
    }
    centerChanged(event: any) {
        this.center = event;
        this.centerMarker = {
            lat: this.center.lat,
            lng: this.center.lng,
            opacity: 1.0,
            draggable: false,
            url: "img/center.png",
            restriction: [[]]
        }
    }
    clickOtherMarker(markerClicked: marker) {
        this.markerInfo = markerClicked;
        this.getInfo.modal('show');
        this.getInfoContent.html(
            "<b>Lat:</b> "+ this.markerInfo.lat + "<br>"+
            "<b>Lng:</b> "+ this.markerInfo.lng + "<br>"+
            "<b>Restr:</b>"+ this.markerInfo.restriction + "<br>"
        );
    }
    doubleClicked(event: any) {
        this.addMarker(event.coords);
    }

    addMarker(coords: any) {
        this.resetForm();
        this.markerToSet = {
            lng: coords.lng,
            lat: coords.lat,
            draggable: true,
            url: null,
            opacity: 0.5,
            restriction: [[]]
        };
        this.addInfo.modal('show');
    }

    gotUser(r: Response) {
        let userContent = r.json();
        return [true, userContent['session']];
    }

    gotNotLoggedIn(e: Response) {
        console.log('Not logged in: ' + e.ok);
        return [];
    }

    getOauth2(res: Response) {
        let resultToken = res.json();
        this.userInformation.setToken(resultToken['access_token']);
        this.userInformation.setRefreshToken(resultToken['refresh_token']);
        this.userInformation.setExpiresIn(resultToken['expires_in']);
        return [true];
    }

    handleTokenError(err: Response) {
        console.log('ERROR TOKEN');
        console.log(err);
        console.log(err.headers);
        console.log(err.url);
        return [];
    }

    gotData(res: Response) {
        console.log(res);
        let result = res.json();
        return result || [];
    }

    gotError(err: Response) {
        console.log(err.ok);
        return [];
    }
}


